.chat-container {
    width: 80vw; /* 屏幕宽度的 2/3 */
    max-width: 1200px; /* 增加最大宽度，以适应更大的屏幕 */
    min-width: 300px; /* 设置最小宽度，以确保在小屏幕上的可用性 */
    margin: 10px auto 20px; /* 减少顶部边距，保持底部边距 */
    padding: 30px;
    background-color: rgba(240, 240, 240, 0.9);
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    height: 75vh; /* 保持之前的高度 */
    display: flex;
    flex-direction: column;
    position: relative; /* 确保这一行存在 */
    left: 50%;
    transform: translateX(-50%);
}

.chat-history {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow-y: auto;
    margin-bottom: 20px;
    padding: 15px;
    padding-bottom: 60px; /* 增加底部padding */
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    background-image: url('https://www.toptal.com/designers/subtlepatterns/uploads/white_wall.png');
    background-repeat: repeat;
    background-color: rgba(255, 255, 255, 0.9);
}

.message-container {
    display: flex;
    margin-bottom: 35px; /* 进一步增加底部边距 */
    position: relative;
}

.user.message-container {
    flex-direction: row-reverse;
}

.message-wrapper {
    max-width: calc(100% - 70px);
    display: flex;
    flex-direction: column;
    position: relative;
    word-wrap: break-word;
}

.user .message-wrapper {
    align-items: flex-end;
    margin-right: 0; /* 移除右边距 */
}

.ai .message-wrapper {
    align-items: flex-start;
    margin-left: 10px;
}

.message {
    padding: 10px 15px;
    border-radius: 20px;
    word-wrap: break-word;
}

.user .message {
    background-color: #007bff;
    color: white;
    border-top-right-radius: 0;
    text-align: right;
}

.ai .message {
    background-color: #f0f0f0;
    color: #333;
    border-top-left-radius: 0;
    text-align: left;
}

.avatar-container {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
}

.avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.user .avatar-container {
    margin-left: 10px; /* 在头像和消息之间添加间距 */
}

.ai .avatar-container {
    margin-right: 10px;
}

.message-actions {
    margin-top: 8px;
    position: absolute;
    bottom: -25px;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 10;
}

.message-container:hover .message-actions {
    opacity: 1;
}

.message-actions button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
    margin-left: 5px;
    padding: 2px;
    transition: transform 0.1s;
}

.message-actions button:hover {
    transform: scale(1.2);
}

.message-actions button:active {
    transform: scale(0.9);
}

.user .message-actions {
    bottom: -20px;
    right: 0;
}

.ai .message-actions {
    right: 0;
}

/* 新增：调整机器人的 message-actions 位置 */
.ai .message-actions {
    bottom: -30px; /* 将器人的操作按钮下移 */
}

.input-area {
    display: flex;
    gap: 10px;
    margin-top: 20px;
    align-items: flex-start;
    flex-shrink: 0;
}

.input-wrapper {
    flex: 1;
    position: relative;
    display: flex;
    flex-direction: column;
}

input {
    width: 100%;
    padding: 10px;
    padding-right: 30px;
    border: 1px solid #ccc;
    border-radius: 20px;
    font-size: 16px;
    box-sizing: border-box;
}

.input-suffix {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
}

.clear-icon {
    width: 20px;
    height: 20px;
    fill: #999;
    cursor: pointer;
    transition: fill 0.3s;
}

.clear-icon:hover {
    fill: #666;
}

.char-count {
    font-size: 12px;
    color: #666;
    position: absolute;
    left: 10px;
    bottom: -20px;
}

.send-button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    white-space: nowrap;
    align-self: flex-start;
}

.send-button:hover {
    background-color: #0056b3;
}

@media (max-width: 768px) {
    .chat-container {
        width: 90vw; /* 在小屏幕上占据更多宽度 */
        padding: 20px; /* 减少内边距 */
    }
}

@media (min-width: 1800px) {
    .chat-container {
        max-width: 1400px; /* 在超宽屏幕上增加最大宽度 */
    }
}

/* 添加以下新样式 */
.user.message-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.user .message-wrapper {
    order: 1; /* 将消息放在第一位 */
}

.user .avatar-container {
    order: 2; /* 将头像放在第二位 */
}

.message-actions {
    margin-top: 8px;
    position: absolute;
    bottom: -25px;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 10;
}

.user .message-actions {
    bottom: -20px;
    right: 0;
}

/* 确保输入区域不会被挤压 */
.input-area {
    flex-shrink: 0;
    margin-bottom: 20px; /* 在输入区域下方添加一些空白 */
}

/* 在 App.css 中添加以下样式 */
.App {
    padding-bottom: 40px; /* 在整个应用底部添加一些空白 */
}

.like-animation,
.dislike-animation {
    position: absolute;
    font-size: 14px;
    font-weight: bold;
    opacity: 0;
    animation: fadeUpAndOut 1s ease-out;
}

.like-animation {
    color: #4caf50;
}

.dislike-animation {
    color: #f44336;
}

@keyframes fadeUpAndOut {
    0% {
        opacity: 0;
        transform: translateY(0);
    }
    20% {
        opacity: 1;
        transform: translateY(-10px);
    }
    80% {
        opacity: 1;
        transform: translateY(-20px);
    }
    100% {
        opacity: 0;
        transform: translateY(-30px);
    }
}

.loading-dots-container {
    position: absolute;
    bottom: 80px; /* 将 loading 放在 input-area 的正上方 */
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    margin-bottom: 10px; /* 在 loading 和 input-area 之间添加一些间距 */
    z-index: 1000;
}

.loading-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 5px 10px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.loading-dots .dot {
    width: 8px;
    height: 8px;
    margin: 0 4px;
    background-color: #3498db;
    border-radius: 50%;
    animation: pulse 1.4s infinite ease-in-out both;
}

.loading-dots .dot:nth-child(1) {
    animation-delay: -0.32s;
}

.loading-dots .dot:nth-child(2) {
    animation-delay: -0.16s;
}

@keyframes pulse {
    0%,
    80%,
    100% {
        transform: scale(0);
    }
    40% {
        transform: scale(1);
    }
}

/* 确保输入区域有足够的上边距 */
.input-area {
    margin-top: 20px; /* 减少这个值，为 loading dots 留出适当空间 */
}

.send-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* 保持现有的 .message-wrapper 样式不变 */
.message-wrapper {
    /* 现有的样式 */
}

/* 为机器人消息添加特定样式 */
.message-container:not(.user) .message-wrapper {
    max-width: 90%; /* 调整这个值来缩短机器人消息盒子的宽度 */
    margin-right: auto; /* 将消息靠左对齐 */
}

/* 如果需要，可以为用户消息添加相应的样式以保持对称 */
.message-container.user .message-wrapper {
    max-width: 90%; /* 保持与机器人消息相同的最大宽度 */
    margin-left: auto; /* 将用户消息靠右对齐 */
}
